  justify-content: center;
}
 
.video-background {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  position: absolute;
  object-fit: cover;
}
 
.video-overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  background: linear-gradient(135deg, color-mix(in srgb, var(--color-surface) 85%, transparent), color-mix(in srgb, var(--color-primary) 80%, transparent));
}
 
.video-content {
  padding: var(--spacing-4xl) var(--spacing-2xl);
  z-index: 3;
  position: relative;
  max-width: 48rem;
  text-align: center;
}
 
.video-play-wrapper {
  margin-bottom: var(--spacing-3xl);
}
 
.video-play-button {
  width: 120px;
  border: 4px solid var(--color-surface);
  cursor: pointer;
  height: 120px;
  display: flex;
  background: var(--color-scrim);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  align-items: center;
  border-radius: var(--border-radius-full);
  backdrop-filter: blur(10px);
  justify-content: center;
}
 
.cta-section {
  padding: var(--spacing-4xl) var(--spacing-2xl);
  overflow: hidden;
  position: relative;
  background: linear-gradient(135deg, var(--color-surface), color-mix(in srgb, var(--color-primary) 5%, var(--color-surface)));
}
 
.cta-spotlight {
  margin: 0 auto;
  z-index: 2;
  position: relative;
  max-width: 56rem;
}
 
.cta-card {
  border: 2px solid var(--color-border);
  display: flex;
  padding: var(--spacing-4xl);
  background: var(--color-surface-elevated);
  box-shadow: 0 24px 64px color-mix(in srgb, var(--color-surface) 60%, transparent);
  text-align: center;
  align-items: center;
  border-radius: var(--border-radius-xl);
  flex-direction: column;
}
 
.cta-badge {
  gap: var(--spacing-sm);
  display: inline-flex;
  padding: var(--spacing-sm) var(--spacing-xl);
  background: linear-gradient(135deg, var(--color-accent), var(--color-secondary));
  align-items: center;
  border-radius: var(--border-radius-full);
  margin-bottom: var(--spacing-2xl);
}
 
.cta-title {
  color: var(--color-on-surface);
  font-size: var(--font-size-3xl);
  font-family: var(--font-family-heading);
  font-weight: var(--font-weight-heading);
  line-height: var(--line-height-heading);
  margin-bottom: var(--spacing-lg);
}
 
.cta-subtitle {
  color: var(--color-on-surface-secondary);
  font-size: var(--font-size-lg);
  margin-bottom: var(--spacing-3xl);
}
 
.cta-features {
  gap: var(--spacing-lg);
  width: 100%;
  display: flex;
  margin-bottom: var(--spacing-3xl);
  flex-direction: column;
}
 
.cta-guarantee {
  gap: var(--spacing-lg);
  width: 100%;
  display: flex;
  padding: var(--spacing-xl);
  background: linear-gradient(135deg, color-mix(in srgb, var(--color-secondary) 10%, transparent), color-mix(in srgb, var(--color-accent) 5%, transparent));
  text-align: left;
  align-items: flex-start;
  border-radius: var(--border-radius-md);
  margin-bottom: var(--spacing-3xl);
}
 
.cta-urgency {
  gap: var(--spacing-sm);
  border: 1px solid #f39c12;
  display: inline-flex;
  padding: var(--spacing-md) var(--spacing-xl);
  background: color-mix(in srgb, #f39c12 15%, transparent);
  align-items: center;
  border-radius: var(--border-radius-full);
  margin-bottom: var(--spacing-2xl);
}
 
.cta-button {
  box-shadow: 0 8px 24px color-mix(in srgb, var(--color-primary) 40%, transparent);
  margin-bottom: var(--spacing-lg);
}
 
.cta-disclaimer {
  color: var(--color-on-surface-secondary);
  font-size: var(--font-size-sm);
}
 
.cta-feature-item {
  gap: var(--spacing-md);
  display: flex;
  padding: var(--spacing-lg);
  background: var(--color-surface);
  text-align: left;
  align-items: center;
  border-left: 3px solid var(--color-primary);
  border-radius: var(--border-radius-md);
}
 
.guarantee-title {
  color: var(--color-on-surface);
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-heading);
