}
 
.testimonials-header  .section-title {
  color: var(--color-on-surface);
  margin-bottom: var(--spacing-lg);
}
 
.testimonials-header  .section-subtitle {
  color: var(--color-on-surface-secondary);
}
 
.testimonials-rail::-webkit-scrollbar {
  height: 8px;
}
 
.testimonials-rail::-webkit-scrollbar-track {
  background: var(--color-border);
  border-radius: var(--border-radius-full);
}
 
.testimonials-rail::-webkit-scrollbar-thumb {
  background: var(--color-primary);
  border-radius: var(--border-radius-full);
}
 
.testimonial-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 16px 48px color-mix(in srgb, var(--color-surface) 40%, transparent);
}
 
.testimonial-stars  svg {
  color: var(--color-accent);
}
 
.stats-header  .section-title {
  color: var(--color-on-surface);
  margin-bottom: var(--spacing-lg);
}
 
.stats-header  .section-subtitle {
  color: var(--color-on-surface-secondary);
}
 
.stat-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 32px color-mix(in srgb, var(--color-accent) 20%, transparent);
}
 
.stat-icon  svg {
  color: var(--color-accent);
}
 
.video-play-button:hover {
  transform: scale(1.1);
  box-shadow: 0 16px 48px color-mix(in srgb, var(--color-primary) 40%, transparent);
}
 
.video-play-button  svg {
  color: var(--color-surface);
}
 
.video-content  .section-title {
  color: var(--color-surface);
  margin-bottom: var(--spacing-lg);
}
 
.video-content  .section-subtitle {
  color: var(--color-surface);
  opacity: 0.95;
}
 
.cta-section::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  z-index: 1;
  position: absolute;
  background: radial-gradient(circle at 20% 50%, color-mix(in srgb, var(--color-primary) 8%, transparent) 0%, transparent 50%);
}
 
.cta-section::after {
  right: 0;
  width: 100%;
  bottom: 0;
  height: 100%;
  content: "";
  z-index: 1;
  position: absolute;
  background: radial-gradient(circle at 80% 80%, color-mix(in srgb, var(--color-accent) 8%, transparent) 0%, transparent 50%);
}
 
.cta-badge  svg {
  color: var(--color-on-accent);
}
 
.cta-badge  span {
  color: var(--color-on-accent);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
 
.cta-feature-item  svg {
  color: var(--color-accent);
  flex-shrink: 0;
}
 
.cta-feature-item  span {
  color: var(--color-on-surface);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
}
 
.cta-guarantee  svg {
  color: var(--color-secondary);
  flex-shrink: 0;
}
 
.cta-urgency  svg {
  color: #f39c12;
}
 
.cta-urgency  span {
  color: var(--color-on-surface);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
}
 
.Content {
  font-size: 16px;
  font-family: Inter;
  font-weight: 400;
